<h2>示例</h2>
<h3>加载 ParticleSystem 烟火特效</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" shouldAnimate>
        <vc-primitive-particle v-for="(option, index) of options" :key="index" :image="option.image" :color="option.color" :startColor="option.startColor" :endColor="option.endColor" :particleLife="option.particleLife" :speed="option.speed" :imageSize="option.imageSize" :emissionRate="option.emissionRate" :emitter="option.emitter" :bursts="option.bursts" :lifetime="option.lifetime" :updateCallback="option.updateCallback" :modelMatrix="option.modelMatrix" :emitterModelMatrix="option.emitterModelMatrix" @complete="complete">
        </vc-primitive-particle>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
