<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">particleCanvas</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">options</span>: []
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">var</span> scene = viewer.scene
        scene.debugShowFramesPerSecond = <span class="hljs-literal">true</span>
        Cesium.Math.setRandomNumberSeed(<span class="hljs-number">315</span>)
        <span class="hljs-keyword">this</span>.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(<span class="hljs-number">108.59777</span>, <span class="hljs-number">40.03883</span>))
        <span class="hljs-keyword">this</span>.emitterInitialLocation = <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">100.0</span>)
        <span class="hljs-keyword">this</span>.minimumExplosionSize = <span class="hljs-number">30.0</span>
        <span class="hljs-keyword">this</span>.maximumExplosionSize = <span class="hljs-number">100.0</span>
        <span class="hljs-keyword">this</span>.particlePixelSize = <span class="hljs-keyword">new</span> Cesium.Cartesian2(<span class="hljs-number">7.0</span>, <span class="hljs-number">7.0</span>)
        <span class="hljs-keyword">var</span> burstSize = <span class="hljs-number">400.0</span>
        <span class="hljs-keyword">this</span>.lifetime = <span class="hljs-number">10.0</span>
        <span class="hljs-keyword">var</span> numberOfFireworks = <span class="hljs-number">20.0</span>

        <span class="hljs-keyword">var</span> xMin = <span class="hljs-number">-100.0</span>
        <span class="hljs-keyword">var</span> xMax = <span class="hljs-number">100.0</span>
        <span class="hljs-keyword">var</span> yMin = <span class="hljs-number">-80.0</span>
        <span class="hljs-keyword">var</span> yMax = <span class="hljs-number">100.0</span>
        <span class="hljs-keyword">var</span> zMin = <span class="hljs-number">-50.0</span>
        <span class="hljs-keyword">var</span> zMax = <span class="hljs-number">50.0</span>

        <span class="hljs-keyword">var</span> colorOptions = [
          {
            <span class="hljs-attr">minimumRed</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">green</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">minimumBlue</span>: <span class="hljs-number">0.8</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          },
          {
            <span class="hljs-attr">red</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">minimumGreen</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">minimumBlue</span>: <span class="hljs-number">0.8</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          },
          {
            <span class="hljs-attr">red</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">green</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">minimumBlue</span>: <span class="hljs-number">0.8</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          },
          {
            <span class="hljs-attr">minimumRed</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">minimumGreen</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">blue</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          }
        ]

        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">var</span> i = <span class="hljs-number">0</span>; i &lt; numberOfFireworks; ++i) {
          <span class="hljs-keyword">var</span> x = Cesium.Math.randomBetween(xMin, xMax)
          <span class="hljs-keyword">var</span> y = Cesium.Math.randomBetween(yMin, yMax)
          <span class="hljs-keyword">var</span> z = Cesium.Math.randomBetween(zMin, zMax)
          <span class="hljs-keyword">var</span> offset = <span class="hljs-keyword">new</span> Cesium.Cartesian3(x, y, z)
          <span class="hljs-keyword">var</span> color = Cesium.Color.fromRandom(colorOptions[i % colorOptions.length])

          <span class="hljs-keyword">var</span> bursts = []
          <span class="hljs-keyword">for</span> (<span class="hljs-keyword">var</span> j = <span class="hljs-number">0</span>; j &lt; <span class="hljs-number">3</span>; ++j) {
            bursts.push(
              <span class="hljs-keyword">new</span> Cesium.ParticleBurst({
                <span class="hljs-attr">time</span>: Cesium.Math.nextRandomNumber() * <span class="hljs-keyword">this</span>.lifetime,
                <span class="hljs-attr">minimum</span>: burstSize,
                <span class="hljs-attr">maximum</span>: burstSize
              })
            )
          }

          <span class="hljs-keyword">this</span>.createFirework(offset, color, bursts)
        }

        <span class="hljs-keyword">var</span> camera = viewer.scene.camera
        <span class="hljs-keyword">var</span> cameraOffset = <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">-300.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>)
        camera.lookAtTransform(<span class="hljs-keyword">this</span>.modelMatrix, cameraOffset)
        camera.lookAtTransform(Cesium.Matrix4.IDENTITY)

        <span class="hljs-keyword">var</span> toFireworks = Cesium.Cartesian3.subtract(<span class="hljs-keyword">this</span>.emitterInitialLocation, cameraOffset, <span class="hljs-keyword">new</span> Cesium.Cartesian3())
        Cesium.Cartesian3.normalize(toFireworks, toFireworks)
        <span class="hljs-keyword">var</span> angle = Cesium.Math.PI_OVER_TWO - <span class="hljs-built_in">Math</span>.acos(Cesium.Cartesian3.dot(toFireworks, Cesium.Cartesian3.UNIT_Z))
        camera.lookUp(angle)
      },
      createFirework(offset, color, bursts) {
        <span class="hljs-keyword">var</span> emitterModelMatrixScratch = <span class="hljs-keyword">new</span> Cesium.Matrix4()
        <span class="hljs-keyword">var</span> position = Cesium.Cartesian3.add(<span class="hljs-keyword">this</span>.emitterInitialLocation, offset, <span class="hljs-keyword">new</span> Cesium.Cartesian3())
        <span class="hljs-keyword">var</span> emitterModelMatrix = Cesium.Matrix4.fromTranslation(position, emitterModelMatrixScratch)
        <span class="hljs-keyword">var</span> particleToWorld = Cesium.Matrix4.multiply(<span class="hljs-keyword">this</span>.modelMatrix, emitterModelMatrix, <span class="hljs-keyword">new</span> Cesium.Matrix4())
        <span class="hljs-keyword">var</span> worldToParticle = Cesium.Matrix4.inverseTransformation(particleToWorld, particleToWorld)

        <span class="hljs-keyword">var</span> size = Cesium.Math.randomBetween(<span class="hljs-keyword">this</span>.minimumExplosionSize, <span class="hljs-keyword">this</span>.maximumExplosionSize)
        <span class="hljs-keyword">var</span> particlePositionScratch = <span class="hljs-keyword">new</span> Cesium.Cartesian3()
        <span class="hljs-keyword">var</span> force = <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">particle</span>) </span>{
          <span class="hljs-keyword">var</span> position = Cesium.Matrix4.multiplyByPoint(worldToParticle, particle.position, particlePositionScratch)
          <span class="hljs-keyword">if</span> (Cesium.Cartesian3.magnitudeSquared(position) &gt;= size * size) {
            Cesium.Cartesian3.clone(Cesium.Cartesian3.ZERO, particle.velocity)
          }
        }

        <span class="hljs-keyword">var</span> normalSize = (size - <span class="hljs-keyword">this</span>.minimumExplosionSize) / (<span class="hljs-keyword">this</span>.maximumExplosionSize - <span class="hljs-keyword">this</span>.minimumExplosionSize)
        <span class="hljs-keyword">var</span> minLife = <span class="hljs-number">0.3</span>
        <span class="hljs-keyword">var</span> maxLife = <span class="hljs-number">1.0</span>
        <span class="hljs-keyword">var</span> life = normalSize * (maxLife - minLife) + minLife
        <span class="hljs-keyword">this</span>.options.push({
          <span class="hljs-attr">image</span>: <span class="hljs-keyword">this</span>.getImage(),
          <span class="hljs-attr">startColor</span>: color,
          <span class="hljs-attr">endColor</span>: color.withAlpha(<span class="hljs-number">0.0</span>),
          <span class="hljs-attr">particleLife</span>: life,
          <span class="hljs-attr">speed</span>: <span class="hljs-number">100.0</span>,
          <span class="hljs-attr">imageSize</span>: <span class="hljs-keyword">this</span>.particlePixelSize,
          <span class="hljs-attr">emissionRate</span>: <span class="hljs-number">0</span>,
          <span class="hljs-attr">emitter</span>: <span class="hljs-keyword">new</span> Cesium.SphereEmitter(<span class="hljs-number">0.1</span>),
          <span class="hljs-attr">bursts</span>: bursts,
          <span class="hljs-attr">lifetime</span>: <span class="hljs-keyword">this</span>.lifetime,
          <span class="hljs-attr">updateCallback</span>: force,
          <span class="hljs-attr">modelMatrix</span>: <span class="hljs-keyword">this</span>.modelMatrix,
          <span class="hljs-attr">emitterModelMatrix</span>: emitterModelMatrix
        })
      },
      getImage() {
        <span class="hljs-keyword">let</span> particleCanvas = <span class="hljs-keyword">this</span>.particleCanvas
        <span class="hljs-keyword">if</span> (!Cesium.defined(particleCanvas)) {
          particleCanvas = <span class="hljs-built_in">document</span>.createElement(<span class="hljs-string">'canvas'</span>)
          particleCanvas.width = <span class="hljs-number">20</span>
          particleCanvas.height = <span class="hljs-number">20</span>
          <span class="hljs-keyword">var</span> context2D = particleCanvas.getContext(<span class="hljs-string">'2d'</span>)
          context2D.beginPath()
          context2D.arc(<span class="hljs-number">8</span>, <span class="hljs-number">8</span>, <span class="hljs-number">8</span>, <span class="hljs-number">0</span>, Cesium.Math.TWO_PI, <span class="hljs-literal">true</span>)
          context2D.closePath()
          context2D.fillStyle = <span class="hljs-string">'rgb(255, 255, 255)'</span>
          context2D.fill()
