<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code>是否显示粒子。</td>
</tr>
<tr>
<td>updateCallback</td>
<td>Function</td>
<td></td>
<td><code v-pre>optional</code> 更新回调函数。</td>
</tr>
<tr>
<td>emitter</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 粒子触发器类型。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 4x4转换矩阵，可将粒子系统从模型转换为世界坐标。</td>
</tr>
<tr>
<td>emitterModelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 4x4转换矩阵，用于转换粒子系统局部坐标系内的粒子系统发射器。</td>
</tr>
<tr>
<td>emissionRate</td>
<td>Number</td>
<td><code v-pre>5</code></td>
<td><code v-pre>optional</code> 每秒要发射的粒子数。</td>
</tr>
<tr>
<td>bursts</td>
<td>Array</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> ParticleBurst 数组，在周期性时间发射粒子。</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 粒子系统完成后是否应循环其爆发。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 设置比例尺，以在其粒子寿命期间应用到粒子图像。</td>
</tr>
<tr>
<td>startScale</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 在粒子寿命开始时应用于粒子图像的初始比例。</td>
</tr>
<tr>
<td>endScale</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 在粒子寿命结束时应用于粒子图像的最终比例。</td>
</tr>
<tr>
<td>color</td>
<td>Object|Array|String</td>
<td></td>
<td><code v-pre>optional</code> 设置粒子在其粒子寿命期间的颜色。</td>
</tr>
<tr>
<td>startColor</td>
<td>Object|Array|String</td>
<td></td>
<td><code v-pre>optional</code> 粒子在其生命初期的颜色。</td>
</tr>
<tr>
<td>endColor</td>
<td>Object|Array|String</td>
<td></td>
<td><code v-pre>optional</code> 粒子寿命结束时的颜色。</td>
</tr>
<tr>
<td>image</td>
<td>Object|String</td>
<td></td>
<td><code v-pre>optional</code> 用于广告牌的URI，HTMLImageElement或HTMLCanvasElement。</td>
</tr>
<tr>
<td>imageSize</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 如果设置，则将覆盖用来缩放粒子图像尺寸（以像素为单位）的minimumImageSize和maximumImageSize输入。</td>
</tr>
<tr>
<td>minimumImageSize</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 设置宽度的最小范围，以高度为单位，在该范围之上可以随机缩放粒子图像的尺寸（以像素为单位）。</td>
</tr>
<tr>
<td>maximumImageSize</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 设置最大边界（宽度乘以高度），在该边界以下可以随机缩放粒子图像的尺寸（以像素为单位）。</td>
</tr>
<tr>
<td>speed</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 如果设置，则用该值覆盖minimumSpeed和maximumSpeed输入。</td>
</tr>
<tr>
<td>minimumSpeed</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置以米/秒为单位的最小范围，在该范围上可以随机选择粒子的实际速度。</td>
</tr>
<tr>
<td>maximumSpeed</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置以米/秒为单位的最大范围，在该范围内将随机选择粒子的实际速度。</td>
</tr>
<tr>
<td>lifetime</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 粒子系统发射粒子的时间（以秒为单位）。</td>
</tr>
<tr>
<td>particleLife</td>
<td>Number</td>
<td><code v-pre>5.0</code></td>
<td><code v-pre>optional</code> 如果设置，则使用此值覆盖minimumParticleLife和maximumParticleLife输入。</td>
</tr>
<tr>
<td>minimumParticleLife</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置以秒为单位的粒子寿命的可能持续时间的最小范围，在该范围内可以随机选择粒子的实际寿命。</td>
</tr>
<tr>
<td>maximumParticleLife</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置以秒为单位的粒子生命的可能持续时间的最大范围，在该范围内将随机选择粒子的实际生命。</td>
</tr>
<tr>
<td>mass</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 设置粒子的最小和最大质量（以千克为单位）。</td>
</tr>
<tr>
<td>minimumMass</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置粒子质量的最小范围（以千克为单位）。 粒子的实际质量将被选择为高于此值的随机量。</td>
</tr>
<tr>
<td>maximumMass</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 设置最大粒子质量（以千克为单位）。 粒子的实际质量将选择为低于此值的随机量。</td>
</tr>
</tbody>
</table>
